label {
  font-size: 0.8125rem;
  color: #737373;
}

.input-group-text {
  transition: border-color 0.15s;
}

.input-group-icon {
  height: 1em;
  width: 1em;
}
.input-group-icon > path {
  stroke-width: 10 !important;
}

.input-group-append.order-0 > .input-group-text {
  border-top-left-radius: var(--bs-border-radius);
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: var(--bs-border-radius);
  border-right: 0;
}

.form-control.order-1 {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: var(--bs-border-radius) !important;
  border-bottom-right-radius: var(--bs-border-radius) !important;
  border-left: 0;
}
.form-control:not(:focus) + .input-group-append .input-group-icon path {
  stroke-dashoffset: 0;
}
.form-control:focus {
  background-color: white;
}
.form-control:focus + .input-group-append .input-group-text {
  background-color: white;
  border-color: #0073B8;
  color: #0073B8;
}
.form-control:focus + .input-group-append .input-group-icon > path {
  animation: icon 0.5s linear forwards;
}
@keyframes icon {
  to {
    stroke-dashoffset: 0;
  }
}

.custom-control-label:before, .custom-control-label:after {
  top: 50%;
  transform: translateY(-50%);
}

.extended-radio-label {
  display: block;
  padding: 1rem 1.5rem;
  border: 1px solid #ebebeb;
  text-align: center;
  font-size: 18px;
  color: #737373;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  transition-property: border-color, color;
}
.extended-radio-label:hover, .extended-radio-label:focus {
  text-decoration: none;
  border-color: #0073B8;
  color: #737373;
}

.extended-radio-input {
  display: none;
}
.extended-radio-input:checked + .extended-radio-label {
  border-color: #0073B8;
  color: #0073B8;
}

.form-light .input-group-text {
  background-color: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.25);
  color: rgba(255, 255, 255, 0.5);
}
.form-light .form-control {
  background-color: rgba(255, 255, 255, 0.05);
  border-color: rgba(255, 255, 255, 0.25);
  color: white;
}
.form-light .form-control::placeholder {
  color: rgba(255, 255, 255, 0.5);
}
.form-light .form-control:focus {
  background-color: transparent;
  border-color: #0073B8;
}
.form-light .form-control:focus + .input-group-append .input-group-text {
  background-color: transparent;
}
.form-light label {
  color: rgba(255, 255, 255, 0.5);
}

.form-styled {
  padding: 2.5rem 1.5rem;
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-top: 2px solid #0073B8;
  box-shadow: 0 25px 100px -5px rgba(0, 0, 0, 0.15);
}
@media (min-width: 768px) {
  .form-styled {
    padding: 3.5rem 2.5rem;
  }
}

#mce-hidden-input {
  display: none;
}

.img-effect {
  position: relative;
}
.img-effect > img {
  position: relative;
  box-shadow: 0 25px 100px -5px rgba(0, 0, 0, 0.15);
  z-index: 1;
}

.img-effect-solid {
  padding-left: 20%;
  padding-right: 10%;
}
.img-effect-solid:before {
  content: "";
  position: absolute;
  top: 35%;
  bottom: 10%;
  left: 0;
  right: 0;
  background-color: #338fc6;
}

.img-effect-border {
  padding-left: 10%;
  padding-right: 20%;
}
.img-effect-border:before {
  content: "";
  position: absolute;
  top: 75%;
  bottom: -10%;
  left: 35%;
  right: 10%;
  background-color: #338fc6;
}
.img-effect-border:after {
  content: "";
  position: absolute;
  top: -10%;
  bottom: 75%;
  left: 0;
  right: 55%;
  border: 2px solid #338fc6;
}

@media (min-width: 768px) {
  .img-effect-info {
    padding-left: 15%;
    padding-right: 15%;
    padding-bottom: 10%;
  }
}
.img-effect-info-popup {
  margin-bottom: 1.5rem;
  padding: 2rem 1.5rem;
  border-radius: 0.25rem;
  background-color: #0073B8;
  text-align: center;
  color: white;
  box-shadow: 0 25px 100px -5px rgba(0, 0, 0, 0.15);
  z-index: 2;
}
@media (min-width: 768px) {
  .img-effect-info-popup {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 50%;
    height: auto;
    margin-bottom: 0;
  }
}
.img-effect-info-popup-icon {
  width: 3rem;
  height: 3rem;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1rem;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 50%;
}

.img-effect-dotted {
  padding: 10% 15% 0 15%;
}
.img-effect-dotted:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 50%;
  left: 50%;
  background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100' viewBox='0 0 100 100'><circle cx='50' cy='50' r='15' fill='%230073B8'></circle></svg>");
  background-size: 0.5rem;
}

.img-incline {
  clip-path: polygon(0% 0%, 100% 0%, 95% 100%, 0% 100%);
}

.img-incline-left {
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 5% 100%);
}

.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-cover-top {
  object-position: center top;
}

.img-cover-right {
  object-position: right center;
}

.img-cover-bottom {
  object-position: center bottom;
}

.img-cover-left {
  object-position: left center;
}

.img-half {
  position: relative;
}
@media (min-width: 768px) {
  .img-half {
    width: 50vw;
    height: 100%;
    margin-left: -1.5rem;
  }
}

@media (min-width: 768px) {
  .img-half-left {
    margin-left: calc(100% - 50vw + 1.5rem);
  }
}

.img-half .img-cover {
  position: absolute;
}
@media (max-width: 575.98px) {
  .img-half .img-cover {
    position: relative;
    max-width: 100%;
    height: auto;
  }
}

.img-square {
  position: relative;
  padding-bottom: 100%;
}

.img-square .img-cover {
  position: absolute;
}

.modal-dialog-video .modal-content {
  background-color: transparent;
  border: none;
  color: white;
  box-shadow: none;
}
.modal-dialog-video .close {
  color: white !important;
  text-shadow: none;
}

.modal.fade .modal-dialog {
  transform: scale(0.95);
  opacity: 0;
}

.modal.show .modal-dialog {
  transform: scale(1);
  opacity: 1;
}

.modal-body {
  padding-top: 0;
}

.nav .nav-link {
  position: relative;
  padding: 0.75rem 1.5rem;
  font-size: 0.6875rem;
  font-weight: 500;
  letter-spacing: 0;
  text-transform: uppercase;
  color: #292929;
}

.nav-pills .nav-link::before {
  content: "";
  position: absolute;
  right: 1.5rem;
  bottom: 0;
  left: 1.5rem;
  border-top: 3px solid #0073B8;
  opacity: 0;
  transform: translateY(0.25rem);
  transition: all 0.2s ease-in-out;
}
.nav-pills .nav-link.active::before {
  opacity: 1;
  transform: translateY(0);
}

.nav-tabs .nav-link:not(.active):hover, .nav-tabs .nav-link:not(.active):focus {
  border-color: #f9f9f9 #f9f9f9 rgba(0, 0, 0, 0.1) #f9f9f9;
  background-color: #f9f9f9;
}

.nav-overflow {
  flex-wrap: nowrap;
  overflow-x: auto;
}
.nav-overflow::-webkit-scrollbar {
  display: none;
}

.navbar:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: white;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  opacity: 0;
  transition: opacity 0.15s linear;
  z-index: -1;
}

.navbar-dark {
  color: white;
}
.navbar-dark > .container {
  position: relative;
}
.navbar-dark > .container:after {
  content: "";
  position: absolute;
  bottom: -1.25rem;
  left: 0;
  right: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.navbar-dark .navbar-brand-svg-i {
  color: #0073B8;
}

.navbar-light {
  color: #292929;
}
.navbar-light:before {
  opacity: 1;
}
.navbar-light .navbar-brand-svg-i {
  color: #0073B8;
}

.navbar-brand {
  line-height: 1.75rem;
  font-weight: 500;
  color: inherit !important;
}

.navbar-brand-svg {
  height: 1.75rem;
  width: auto;
  width: 5.140625rem;
}

.navbar-brand-img {
  height: 1.75rem;
  width: auto;
}

.navbar-nav .nav-link {
  position: relative;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 0.75rem;
  font-weight: 500;
  letter-spacing: 0;
  text-transform: uppercase;
  transition: all 0.2s ease-in-out;
  transition-property: color;
}
@media (min-width: 1200px) {
  .navbar-nav .nav-link:before {
    display: none;
    content: "";
    position: absolute;
    bottom: calc(-1.25rem);
    left: 1rem;
    right: 1rem;
    border-top: 3px solid #0073B8;
  }
  .navbar-nav .nav-link:hover, .navbar-nav .nav-link:focus {
    outline: none;
  }
}
@media (min-width: 1200px) {
  .navbar-nav .nav-link.active:before {
    display: block;
  }
}

@media (min-width: 1200px) {
  .navbar-nav .nav-item.active > .nav-link:before {
    display: block;
  }
}

@media (max-width: 991.98px) {
  .navbar-nav .dropdown-menu {
    padding-top: 0;
    padding-bottom: 0;
    border: 0;
  }
}
@media (min-width: 1200px) {
  .navbar-nav .dropdown-menu {
    transform: translateY(0.5rem);
    opacity: 0;
  }
  .navbar-nav .dropdown-menu.show {
    animation-name: dropdownFadeUp;
  }
}

.navbar-nav .nav-item-divider {
  display: none;
}
@media (min-width: 1200px) {
  .navbar-nav .nav-item-divider {
    display: block;
  }
}
.navbar-nav .nav-item-divider .nav-link {
  opacity: 0.25;
}
.navbar-nav .nav-item-divider .nav-link > span {
  border-left: 1px solid;
}

.page-link {
  font-size: 0.6875rem !important;
  font-weight: 500;
  letter-spacing: 0;
  text-transform: uppercase;
}

html, body {
  scroll-behavior: smooth;
}

::selection {
