.bg-slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: auto;
}
.bg-slider .slider {
  width: 100%;
  height: 100%;
}
.bg-slider .slider-item {
  width: inherit;
  height: inherit;
  padding: 0;
}
.bg-slider .slider .flickity-viewport {
  height: 100% !important;
}

.bg-video {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.bg-video-media {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  transform: translate(-50%, -50%);
}
@supports not (-ms-ime-align: auto) {
  @supports (object-fit: cover) {
    .bg-video-media {
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      min-width: none;
      min-height: none;
      object-fit: cover;
      transform: none;
    }
  }
}

.bg-cover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.bg-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.bg-overlay:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #292929;
  opacity: 0.55;
}

.bg-triangle {
